$blue: #0A2B4E;
$yellow: #FDB827;
$white: #fff;

// Mixins
@mixin transition($transitions) {
     -moz-transition: $transitions;
       -o-transition: $transitions;
      -ms-transition: $transitions;
  -webkit-transition: $transitions;
          transition: $transitions;
}

// Nav
#masthead {
  .navbar-brand {
    img { 
      padding-top: 15px;
      padding-bottom: 15px;
      width: 200px;
      transition: all .2s ease-in-out;
    }
  }
  &.sticky {
    position: fixed;
    width: 100%;
    left: 0;
    top: 0;
    z-index: 100;
    border-top: 0;
    .navbar-brand {
      img {
        padding-top: 0;
        padding-bottom: 0;
      }
    }
  }
  .navbar-nav {
    .nav-item {
      padding-left: 0.5rem;
      padding-right: 0.5rem;
    }
    .nav-item:last-child {
      padding-right: 0;
      a {
        padding: 10px 25px;
        border-radius: 25px;
        border: 2px solid $yellow;
        color: $white;
        background-color: $yellow;
        text-decoration: none;
        text-transform: uppercase;
        display: inline-block;
        font-size: 0.8rem;
        font-weight: bold;
        &:hover {
          background-color: lighten($yellow,10%)
        }
      }
    }
  }
  @media screen and (max-width:767px) {
    .navbar-collapse {
      .navbar-nav {
        padding-bottom: 2rem;
      }
      .nav-item {
        text-align: center;
      }
    }
  }
}

// Footer
footer#colophon {
  background-color: $blue;
  color: #fff;
  padding-top: 2rem;
  padding-bottom: 2rem;
  h2, h4 {
    font-size: 1.25rem;
  }
  a {
    color: #fff;
    text-decoration: none;
    &:hover, &:visited, &:active {
      color: #fff;
      text-decoration: none;
    }
  }
  .site-info {
    font-size: 0.75rem;
    text-align: center;
  }
  .footerLeftCoInfo {
    p, address {
      margin-bottom: 0;
    }
  }
  .footerSocialList {
    margin-left: 0;
    text-align: center;
    li {
      display: inline-block;
      a {
        width: 60px;
        height: 60px;
        background: $yellow;
        padding: 1rem;
        text-align: center;
        border-radius: 50%;
        color: $white;
        margin-left: 5px;
        margin-right: 5px;
        display: inline-block;
        i {
          font-size: 1.5rem;
          color: $white;
        }
      }
    }
  }
}

// General
body {
  font-family: 'Libre Baskerville', serif;
  h2 {
    margin-bottom: 2rem;
  }
  p, li, address {
    font-family: 'Open Sans', Arial, sans-serif;
    font-weight: 300;
  }
  ul {
    margin-left: 0;
    text-align: left;
  }
  hr {
    border-top: 1px solid #ddd;
  }
}

section {
  padding-top: 2rem;
  padding-bottom: 2rem;
}

.button {
  padding: 10px 25px;
  border-radius: 25px;
  border: 2px solid $blue;
  color: $blue;
  text-decoration: none;
  text-transform: uppercase;
  display: inline-block;
  font-size: 0.8rem;
  font-weight: bold;
  @include transition(all .2s ease-in-out);
  &:hover {
    text-decoration: none;
    background-color: $blue;
    color: $white;
  }
  &:visited {
    color: $blue;
    &:hover {
      color: $white;
    }
  }
  &.cta {
    color: $white;
    background-color: $yellow;
    border-color: $yellow;
    &:hover {
      background-color: transparent;
      color: $yellow;
    }
  }
  &.white {
    color: $white;
    background-color: transparent;
    border-color: $white;
    &:hover {
      background-color: $white;
      color: $blue;
    }
  }
}

#singleCustomBreadcrumbs {
  margin-top: 1rem;
  text-transform: uppercase;
  font-size: 0.8rem;
}

.singleCustomHeader {
  margin-top: 2rem;
  margin-bottom: 1rem;
}

.slick-dots {
  margin-left: 0;
  padding-left: 0;
  text-align: center;
  list-style: none;
  li {
    display: inline-block;
    width: 10px;
    height: 10px;
    content: ' ';
    padding: 0;
    color: transparent;
    border-radius: 50%;
    border: 0;
    margin: 5px;
    background: #ddd;
    button {
      display: none;
    }
    &.slick-active {
      background-color: #999;
    }
    &:hover {
      cursor: pointer;
    }
    &:focus, &:active {
      outline: 0;
    }
  }
}

.headingSpacer {
  background-color: #ddd;
  width: 75px;
  height: 2px;
  margin-top: 2rem;
  margin-bottom: 1rem;
}

.parallax-window {
    background: transparent;
}

#map-canvas {
  height: 450px;
  width: 100%;
  .gm-style img {
    max-width: 100%;
  }
  h3, p {
    font-size: 1rem;
  }
}

// Pages
#pageIntro {
  padding-top: 6rem;
  padding-bottom: 2rem;
  h1 {
    color: $yellow;
    font-weight: bold;
  }
  .pageBread {
    color: $yellow;
    text-transform: uppercase;
    font-weight: 100;
    font-size: 0.8rem;
    a { color: $yellow; }
  }
}
#footMap {
  padding-top: 0;
  padding-bottom: 0;
}